  data: () => ({
    resultPreview: null,
    recordingPreview: null,
  }),
  mounted() {
    this.recordingPreview = document.getElementById("recordingPreview");
    this.resultPreview = document.getElementById("resultPreview");
  },
  methods: {
    startA() {
      videoCaptureModule()
        .start(this.recordingPreview, this.resultPreview)
        .then((stream) => {})
        .catch((error) => {});
    },
    stop() {
      if (this.recordingPreview != null)
        videoCaptureModule().stop(this.recordingPreview.srcObject);
      clearInterval(this.interval);
    },
  },
};
</script>
