const videoCaptureModuleA = () => {

    const mimeType = 'video/mp4'
    const fileException = 'video/mp4'
    const fileName = 'some.mp4'

    // Put variables in global scope to make them available to the browser console.
    const constraints = window.constraints = {
        audio: true,
        video: true,
        controls: true,
        width: 320,
        height: 240,
        fluid: true,
        muted: false,
        plugins: {
            record: {
                audio: true,
                video: true,
                maxLength: 15,
                videoMimeType: "video/webm"
            }
        },
        html5: {
            nativeTextTracks: false
        },
        controlBar: {
            fullscreenToggle: false,
        }
    }

    const render = (stream) => {
        let options = {mimeType: "video/webm; codecs=vp9"};
        let recorder = new MediaRecorder(stream, options)
        let data = []

        recorder.ondataavailable = event => data.push(event.data)
        recorder.start()

        return new Promise((resolve, reject) => {
            recorder.onstop = resolve
            recorder.onerror = event => reject(event.name)
        }).then(() => data).catch((e) => console.log(e))
    }

    const start = (elCapture, elPreview) => new Promise(function (resolve, reject) {
        try {
            navigator.mediaDevices.getUserMedia(constraints).then(stream => {
                elCapture.srcObject = stream
                elCapture.captureStream = elCapture.captureStream || elCapture.mozCaptureStream
                return new Promise(resolve => elCapture.onplaying = resolve);
            }).then(() => render(elCapture.captureStream()))
                .then(recordedChunks => {
                    let recordedBlob = new Blob(recordedChunks, {type: fileException})
                    recordedBlob.name = fileName
                    elPreview.src = URL.createObjectURL(recordedBlob)
                    resolve(recordedBlob)
                })
                .catch((error) => reject(error))
        } catch (e) {
            reject(e)
        }
    })

    const stop = (stream) => {
        if (stream != null) {
            stream.getTracks().forEach(track => track.stop());
        }
    }

    return {
        start,
        stop,
    }

}

export default videoCaptureModuleA
